<template>
  <div class="p52dNoiseWalker noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      t: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)
        this.t = 0
      }

      p.draw = () => {
        // walker fades away
        // p.background(255, 10);

        let x = p.width * p.noise(this.t)
        let y = p.height * p.noise(this.t + 5)

        let r = 255 * p.noise(this.t + 10)
        let g = 255 * p.noise(this.t + 15)
        let b = 255 * p.noise(this.t + 20)

        p.noStroke()
        p.fill(r, g, b)
        p.ellipse(x, y, 120, 120)

        this.t = this.t + 0.01
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
