<template>
  <div class="p52dBouncingBall noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      location: null,
      velocity: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)

        this.location = p.createVector(100, 100)
        this.velocity = p.createVector(2.5, 5)
      }

      p.draw = () => {
        p.background(255, 50)

        this.location.add(this.velocity)

        if (this.location.x > p.width - 25 || this.location.x < 25) {
          this.velocity.x = this.velocity.x * -1
        }

        if (this.location.y > p.height - 25 || this.location.y < 25) {
          this.velocity.y = this.velocity.y * -1
        }

        p.noStroke()
        p.fill('#42b983')
        p.ellipse(this.location.x, this.location.y, 50, 50)
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
