<template>
  <div class="p5OscillationSimple noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)
      }

      p.draw = () => {
        p.background(255)

        let period = 120
        let amplitude = 100

        let x = amplitude * p.cos((p.TWO_PI * p.frameCount) / period)

        p.stroke(0)
        p.fill('#42b983')
        p.translate(p.width / 2, p.height / 2)
        p.line(0, 0, x, 0)
        p.ellipse(x, 0, 20, 20)
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
