<template>
  <div class="p52dRandomWalker noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      myWalker: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)

        this.myWalker = new Walker()
      }

      p.draw = () => {
        this.myWalker.display()
        this.myWalker.step()
      }

      class Walker {
        constructor () {
          this.x = p.width / 2
          this.y = p.height / 2
        }

        display () {
          p.stroke('#42b983')
          p.point(this.x, this.y)
        }

        step () {
          let stepx = p.random(-1, 1)
          let stepy = p.random(-1, 1)

          this.x += stepx
          this.y += stepy
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
