<template>
  <div class="p5OscillatorObjects noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      oscillatorObjects: new Array(7)
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)

        for (let i = 0; i < this.oscillatorObjects.length; i++) {
          this.oscillatorObjects[i] = new Oscillator()
        }
      }

      p.draw = () => {
        p.background(255)

        for (let i = 0; i < this.oscillatorObjects.length; i++) {
          this.oscillatorObjects[i].display()
          this.oscillatorObjects[i].oscillate()
        }
      }

      class Oscillator {
        constructor () {
          this.angle = p.createVector()
          this.velocity = p.createVector(
            p.random(-0.05, 0.05),
            p.random(-0.05, 0.05)
          )
          this.amplitude = p.createVector(
            p.random(p.width / 2),
            p.random(p.height / 2)
          )
        }

        oscillate () {
          this.angle.add(this.velocity)
        }

        display () {
          let x = p.sin(this.angle.x) * this.amplitude.x
          let y = p.sin(this.angle.y) * this.amplitude.y

          p.push()

          p.translate(p.width / 2, p.height / 2)
          p.stroke(0)
          p.fill('#42b983')
          p.line(0, 0, x, y)
          p.ellipse(x, y, 16, 16)

          p.pop()
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
