<template>
  <div class="p5GravitationalAttraction noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      movers: new Array(5),
      attractor: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)

        for (let i = 0; i < this.movers.length; i++) {
          this.movers[i] = new Mover(
            p.random(0.5, 3),
            p.width / 2 + p.random(-100, 100),
            p.height / 2 - p.random(-100, 100)
          )
        }

        this.attractor = new Attractor()
      }

      p.draw = () => {
        p.background(255)

        this.attractor.display()

        for (let i = 0; i < this.movers.length; i++) {
          let force = this.attractor.attract(this.movers[i])
          this.movers[i].applyForce(force)
          this.movers[i].update()
          this.movers[i].display()
        }
      }

      class Mover {
        constructor (m, x, y) {
          this.location = p.createVector(x, y)
          this.velocity = p.createVector(p.random(1, 4), p.random(1, 4))
          this.acceleration = p.createVector(0, 0)
          this.mass = m
        }

        applyForce (force) {
          let f = P5.Vector.div(force, this.mass)
          this.acceleration.add(f)
        }

        update () {
          this.velocity.add(this.acceleration)
          this.location.add(this.velocity)
          this.acceleration.mult(0) // clear the acceleration each time
        }

        display () {
          p.stroke(0)
          p.fill('#42b983')
          p.ellipse(
            this.location.x,
            this.location.y,
            this.mass * 16,
            this.mass * 16
          )
        }
      }

      class Attractor {
        constructor () {
          this.location = p.createVector(p.width / 2, p.height / 2)
          this.mass = 50
          this.G = 0.7
        }

        attract (mover) {
          let force = P5.Vector.sub(this.location, mover.location)
          let distance = force.mag()

          // contrain the distance so the object doesn't spin out of control
          distance = p.constrain(distance, 5.0, 25.0)

          force.normalize()
          let strength =
            (this.G * this.mass * mover.mass) / (distance * distance)
          force.mult(strength)

          return force
        }

        display () {
          p.stroke(0)
          p.fill(175, 200)
          p.ellipse(
            this.location.x,
            this.location.y,
            this.mass * 2,
            this.mass * 2
          )
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
