<template>
  <div class="p5GravityWindFriction noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      movers: new Array(25)
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)

        for (let i = 0; i < this.movers.length; i++) {
          this.movers[i] = new Mover(p.random(0.1, 5), 0, 0)
        }
      }

      p.draw = () => {
        p.background(255)

        for (let i = 0; i < this.movers.length; i++) {
          // friction
          let c = 0.01
          let normal = 1
          let frictionMag = c * normal
          let friction = this.movers[i].velocity.copy()
          friction.mult(-1)
          friction.normalize()
          friction.mult(frictionMag)

          // constant wind
          let wind = p.createVector(0.01, 0)

          // scale gravity by mass
          let gravity = p.createVector(0, 0.1 * this.movers[i].mass)

          // apply forces
          this.movers[i].applyForce(friction)
          this.movers[i].applyForce(wind)
          this.movers[i].applyForce(gravity)

          this.movers[i].update()
          this.movers[i].display()
          this.movers[i].checkEdges()
        }
      }

      class Mover {
        constructor (m, x, y) {
          this.location = p.createVector(x, y)
          this.velocity = p.createVector(0, 0)
          this.acceleration = p.createVector(0, 0)
          this.mass = m
        }

        applyForce (force) {
          let f = P5.Vector.div(force, this.mass)
          this.acceleration.add(f)
        }

        update () {
          this.velocity.add(this.acceleration)
          this.location.add(this.velocity)
          this.acceleration.mult(0) // clear the acceleration each time
        }

        display () {
          p.stroke(0)
          p.fill('#42b983')
          p.ellipse(
            this.location.x,
            this.location.y,
            this.mass * 16,
            this.mass * 16
          )
        }

        checkEdges () {
          if (this.location.x > p.width - this.mass * 8) {
            this.location.x = p.width - this.mass * 8
            this.velocity.x *= -1
          } else if (this.location.x < 0) {
            this.velocity.x *= -1
            this.location.x = 0
          }

          if (this.location.y > p.height - this.mass * 8) {
            // reverse the direction of our object when it reaches the edge.
            this.velocity.y *= -1
            this.location.y = p.height - this.mass * 8
          }
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
