<template>
  <div class="p5NoiseWave noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      startAngle: 0,
      angleVel: 0.03
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
      }

      p.draw = () => {
        p.background(255)

        let angle = this.startAngle

        for (let x = 0; x <= p.width; x += 12) {
          let y = p.map(p.noise(angle), -1, 1, 0, p.height)
          p.stroke(0)
          p.fill(`rgba(66,185,131, .5)`)
          p.ellipse(x, y, 48, 48)
          angle += this.angleVel
        }

        this.startAngle += 0.01
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
