<template>
  <div class="p5StaticWave noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5StaticWave"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight / 4)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)
        p.background(255)
      }

      p.draw = () => {
        let angle = 0
        let angleVel = 0.2

        p.background(255)

        p.stroke('#42b983')
        p.strokeWeight(2)
        p.noFill()

        p.beginShape()

        for (let x = 0; x <= p.width; x += 5) {
          let y = p.map(p.sin(angle), -1, 1, 0, p.height)
          p.vertex(x, y)
          angle += angleVel
        }

        p.endShape()
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight / 4)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>

<style lang="scss" scoped>
.canvasP5StaticWave {
  display: block;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  margin-top: 10%;
}
</style>
