<template>
  <div class="p5ParticleSystemForces noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <h3>Play with multiple forces.</h3>
    <label for="gravity">Gravity</label>
    <input type="checkbox" name="gravity" id="gravity" />
    <label for="wind">Wind</label>
    <input type="checkbox" name="wind" id="wind" checked />
    <label for="windLeft">Wind Velocity</label>
    <button
      id="windLeft"
      name="windLeft"
      v-bind:class="{ currentDirection: windLeft }"
    >
      &lt;
    </button>
    <button id="windRight" v-bind:class="{ currentDirection: windRight }">
      &gt;
    </button>
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      system: null,
      gravity: null,
      wind: null,
      windVelocity: null,
      windLeft: null,
      windRight: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)

        this.system = new ParticleSystem(
          p.createVector(p.width / 2, p.height / 1.75)
        )

        this.windVelocity = p.createVector(0.005, 0)
        this.gravity = false
        this.wind = true
        this.windLeft = false
        this.windRight = true
      }

      p.draw = () => {
        p.background(255)

        if (this.gravity) {
          // apply force of gravity to all particles
          let gravity = p.createVector(0, 0.01)
          this.system.applyForce(gravity)
        }

        if (this.wind) {
          // apply force of wind to all particles
          this.system.applyForce(this.windVelocity)
        }

        this.system.addParticle()
        this.system.run()
      }

      class ParticleSystem {
        constructor (location) {
          this.origin = location.copy()
          this.particles = []
        }

        addParticle () {
          let r = p.random(1)
          if (r < 0.5) {
            this.particles.push(new Particle(this.origin))
          } else {
            this.particles.push(new Confetti(this.origin))
          }
        }

        applyForce (f) {
          for (let i = 0; i < this.particles.length; i++) {
            this.particles[i].applyForce(f)
          }
        }

        run () {
          for (let particle of this.particles) {
            particle.run()
          }

          // remove dead particles
          this.particles = this.particles.filter(
            particle => !particle.isDead()
          )
        }
      }

      class Particle {
        constructor (l) {
          this.location = l.copy()
          this.velocity = p.createVector(p.random(-3, 3), p.random(-2, 0))
          this.acceleration = p.createVector(0, 0.1)
          this.lifespan = 1.0
          this.mass = 1
        }

        run () {
          this.update()
          this.display()
        }

        update () {
          this.velocity.add(this.acceleration)
          this.location.add(this.velocity)
          this.lifespan -= 0.01
        }

        display () {
          p.stroke(`rgba(57, 81, 105, ${this.lifespan})`)
          p.strokeWeight(2)
          p.fill(`rgba(66, 185, 131, ${this.lifespan})`)
          p.ellipse(this.location.x, this.location.y, 32, 32)
        }

        applyForce (force) {
          let f = force.copy()
          f.div(this.mass)
          this.acceleration.add(f)
        }

        isDead () {
          if (this.lifespan < 0.0) {
            return true
          } else {
            return false
          }
        }
      }

      class Confetti extends Particle {
        constructor (l) {
          super(l)
        }

        display () {
          let theta = p.map(this.location.x, 0, p.width, 0, p.TWO_PI * 2)

          p.rectMode(p.CENTER)
          p.fill(`rgba(57, 81, 105, ${this.lifespan})`)
          p.stroke(`rgba(66, 185, 131, ${this.lifespan})`)

          p.push()

          p.translate(this.location.x, this.location.y)
          p.rotate(theta)
          p.rect(0, 0, 16, 32)

          p.pop()
        }
      }

      this.checkWindDirection = () => {
        if (this.windVelocity.x < 0) {
          this.windLeft = true
          this.windRight = false
        } else if (this.windVelocity.x == 0) {
          this.windLeft = false
          this.windRight = false
        } else {
          this.windLeft = false
          this.windRight = true
        }
      }

      document.getElementById('gravity').addEventListener('click', e => {
        this.gravity = e.target.checked
      })

      document.getElementById('wind').addEventListener('click', e => {
        this.wind = e.target.checked
      })

      document.getElementById('windLeft').addEventListener('click', () => {
        this.windVelocity.x -= 0.005
        this.checkWindDirection()
      })

      document.getElementById('windRight').addEventListener('click', () => {
        this.windVelocity.x += 0.005
        this.checkWindDirection()
      })

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>

<style lang="scss" scoped>
input,
button {
  vertical-align: middle;
  height: 1rem;
}
label {
  padding-left: 2rem;
}
#windLeft {
  margin-left: 0.5rem;
}
button {
  line-height: 50%;
  font-weight: 600;
  color: grey;
}
.currentDirection {
  color: green;
}
</style>
