<template>
  <div class="p5ParticleInheritance noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <h3>Click anywhere to add a Particle System!</h3>
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import P5NavBar from '@/components/P5NavBar.vue'

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      systems: null
    }
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight)
        this.canvas.parent(this.$refs.canvas)
        p.frameRate(30)

        this.systems = []
      }

      p.draw = () => {
        p.background(255)

        for (let i = 0; i < this.systems.length; i++) {
          this.systems[i].addParticle()
          this.systems[i].run()
        }
      }

      class ParticleSystem {
        constructor (location) {
          this.origin = location.copy()
          this.particles = []
        }

        addParticle () {
          let r = p.random(1)
          if (r < 0.5) {
            this.particles.push(new Particle(this.origin))
          } else {
            this.particles.push(new Confetti(this.origin))
          }
        }

        run () {
          for (let particle of this.particles) {
            particle.run()
          }

          // remove dead particles
          this.particles = this.particles.filter(
            particle => !particle.isDead()
          )
        }
      }

      class Particle {
        constructor (l) {
          this.location = l.copy()
          this.velocity = p.createVector(p.random(-3, 3), p.random(-2, 0))
          this.acceleration = p.createVector(0, 0.1)
          this.lifespan = 1.0
        }

        run () {
          this.update()
          this.display()
        }

        update () {
          this.velocity.add(this.acceleration)
          this.location.add(this.velocity)
          this.lifespan -= 0.01
        }

        display () {
          p.stroke(`rgba(57, 81, 105, ${this.lifespan})`)
          p.strokeWeight(2)
          p.fill(`rgba(66, 185, 131, ${this.lifespan})`)
          p.ellipse(this.location.x, this.location.y, 32, 32)
        }

        isDead () {
          if (this.lifespan < 0.0) {
            return true
          } else {
            return false
          }
        }
      }

      class Confetti extends Particle {
        constructor (l) {
          super(l)
        }

        display () {
          let theta = p.map(this.location.x, 0, p.width, 0, p.TWO_PI * 2)

          p.rectMode(p.CENTER)
          p.fill(`rgba(57, 81, 105, ${this.lifespan})`)
          p.stroke(`rgba(66, 185, 131, ${this.lifespan})`)

          p.push()

          p.translate(this.location.x, this.location.y)
          p.rotate(theta)
          p.rect(0, 0, 16, 32)

          p.pop()
        }
      }

      p.mousePressed = () => {
        this.systems.push(
          new ParticleSystem(p.createVector(p.mouseX, p.mouseY))
        )
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
      }
    }
    const P5 = require('p5')
    this.ps = new P5(this.script)
    // eslint-disable-next-line
    console.log(this.ps);
  }
}
</script>
